import { getCountryByCCA3 } from '~/api/countries';
import { json, useLoaderData } from 'superjson-remix';
import { LoaderFunction, useParams } from 'remix';

// Language: typescript
// Path: app\routes\$cca3.tsx
export const useCountryByCCA3 = (): LoaderFunction<Country> => {
    const { cca3 } = useParams();
    return useLoaderData(getCountryByCCA3(cca3), json);
    }